@import "variables";

.catalog {
  display: flex;
  justify-content: center;
  width: 1024px;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-bottom: 28px;
  .card {
    @include align_items;
    width: 200px;
    height: 403px;
    border: 1px solid #f3f3f3;
    box-sizing: border-box;
    border-radius: 5px;
    line-height: 13px;
    flex-direction: column;
    margin-right: 48px;
    margin-bottom: 49px;
    &:nth-child(4n) {
      margin-right: 0;
    }
    .img-item {
      width: 160px;
      height: 134px;
      margin: 0 auto;
      margin-bottom: 45px;
      margin-top: 30px;
    }
    .card_about {
      width: 170px;
      .info {
        @include link_info_size;
        margin: 0;
        margin-bottom: 6px;
        line-height: 15px;
      }
      .code {
        margin-bottom: 15px;
      }
      .stars_comments {
        @include align_items;
        justify-content: space-between;
        margin-bottom: 18px;
        .stars {
          display: flex;
          .star {
            @include star_properties;
            &:last-child {
              background: url(./../images/star.svg) no-repeat center;
            }
          }
        }
      }
      .price {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 20px;
        &_value {
          font-size: 16px;
          font-weight: 700;
          line-height: 19px;
        }
      }
      .link_buy {
        width: 170px;
        height: 40px;
        padding: 11px;
        font-size: 14px;
        font-weight: 700;
        background-color: $link_color_hover;
        border-radius: 5px;
        color: #fff;
        text-decoration: none;
        text-transform: uppercase;
        display: block;
        line-height: 16px;
        box-sizing: border-box;
        text-align: center;
        &:hover {
          background: #fff;
          border: 1px solid $link_color_hover;
          box-sizing: border-box;
          border-radius: 5px;
          color: $link_color_hover;
        }
      }
    }
  }
}

@media screen and (min-width: 1200px) {
  .catalog {
    padding: 0 88px;
  }
}
